            for(let i = 0; i < this.dataMenu.length;i++){
                if(this.dataMenu.length>0){
                    this.dataMenu[i].page = i;
                    if(i == this.dataMenu.length-1){
                        this.nowData = this.dataMenu[i];
                        this.nowpage =  i;
                    }
                }
